const Icon = (props) => {
  const {color=false, name, url} = props
  return icons
    .filter((icon) => icon.name === name)
    .map((icon) => {
      let svg = (
        <svg
          aria-hidden="true"
          focusable="false"
          data-icon={icon.name}
          role="img"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 576 512"
        >
          <path fill={icon.color&color ? icon.color : 'currentColor'} d={icon.d}></path>
        </svg> 
      )

      let newUrl = url ? url : icon.url
      return icon.url ? (
        <a
          href={newUrl}
          target="_blank"
          className={`icon icon-${icon.name}`}
          key={`icon-${icon.name}`}
          rel="noopener noreferrer"
        >
          {svg}
        </a>
      ) : (
        <span className={`icon icon-${icon.name}`} key={`icon-${icon.name}`}>
          {svg}
        </span>
      );})
}

export default Icon